@font-face {
  font-family: 'DuneRise'; /* Replace with your font's name */
  src: local('DuneRise'), url(./fonts/Dune_Rise.ttf)format('truetype')
}


.landing-page {
  background-color: #222; /* Dark color */
  color: white;
  height: 100vh;        /* Occupy full screen height */
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}



.landing-content h1 {
  font-size: 3rem;   
      font-family: 'DuneRise'; /* Fallback font */
        font-weight: 800;

   /* Adjust header size as desired */
}

@media (max-width: 768px) {
  .landing-content h1 {
    font-size: 2rem;  
    font-family: 'DuneRise'; /* Fallback font */
  }
}



/* landingpage.css */
.begin-button {
  background-color: #BF953F; /* Think of a color that fits your aesthetic */
  color: black;
  padding: 15px 30px;
  border: none;
  border-radius: 5px;  
  font-size: 15px;
  cursor: pointer;
  box-shadow: 2px 3px 5px rgba(0, 0, 0, 0.3); /* Adds a subtle shadow */
  font-weight: 00;
  transition: all 0.3s ease; /* Enables smooth transitions */
  text-transform: capitalize;
;}

.begin-button:hover {
  background-color: #9F7E2F; /* Darker on hover */
  transform: scale(1.1);   /* Slightly increase size */
  box-shadow: 3px 5px 7px rgba(0, 0, 0, 0.4); /* More pronounced shadow */
}

.button-container {
  width: 100%; 
  display: flex;
  justify-content: center; 
  margin-top: 10%;
}

/* landingpage.css */
.animated-title {
  animation: HuntReveal 2s ease-in-out; /* Adjust duration and timing function */
}

@keyframes HuntReveal {
  0% { 
    opacity: 0;
    transform: translateY(20px); /* Initially hidden and slightly below */
  }
  100% {
    opacity: 1;
    transform: translateY(0);  /* Move into final position */
  }
}

.color-shift-title {
  background: linear-gradient(to right, #f09819 0%, #FF512F 100%); 
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent; 
  animation: colorShift 5s infinite alternate;
}

@keyframes colorShift {
  0% { background-position: 0%; }
  100% { background-position: 100%; }
}
