/* webcam.css */

/* Container for styling the entire webcam area (optional) */
.webcam-container {
  display: flex;
  height: 100vh;
  flex-direction: column;
  align-items: center;
  margin: 20px; /* Add spacing as needed */
}

/* The webcam video element */
video {
  width: 400px; /* Adjust as desired  */
   border: 5px solid #9F7E2F !important; /* Force a very noticeable border */
  margin-bottom: 10px; 
}

/* Style your buttons */
button {
  background-color: #007bff; 
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

button:not(:disabled):hover { /* Apply hover only to enabled buttons */
  background-color: #0056b3; /* Slightly darker on hover */
}

button:disabled {
  opacity: 50%; /* Slightly darker on hover */
}


.cam-buttons{
gap:2%;
display: flex;
justify-content: center;

}

.loader {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%; /* Adjust as needed */
}