.card {
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  margin: 25px;
  transition: 0.4s ease-in-out;
  flex: 0 0 auto;  /* Prevent cards from shrinking */
  width: 280px;
    height: 40vh;
    border: solid 3px #272727;
    border-radius: 19px;
    padding: 1.5rem;
    position: relative;
    box-shadow: 0px 7px 10px rgba(0, 0, 0, 0.5);
    align-items: center;
}

.card-img {
  width: 100%;
  height: 100px; /* Adjust as needed */
  object-fit: cover;
  border-radius: 5px 5px 0 0;
}

.card-content {
  padding: 10px;
}

/* Media Queries for Responsiveness */
@media (max-width: 768px) {
  .card {
    width: calc(50% - 20px); /* Two cards per row */
  }
}

@media (max-width: 480px) {
  .card {
    width: 60%; /* Single card per row */
  }
}

.card-container { /* Container for the entire display of cards */
   display: flex;
    background: linear-gradient( to right, #000,#808080, skyblue);
 flex-wrap: nowrap;
   width: 100vw;
   align-items: center;
   justify-content: center;
   overflow-x: hidden;  /* Enable horizontal scrolling if needed */
    overflow-y: hidden;  /* Enable horizontal scrolling if needed */
}

.card:hover {
  transform: scale(1.05) perspective(500px) rotateY(5deg); /* Adjust angles as desired */
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);  /* Enhance shadow on hover */
  z-index: 2;  /* Bring hovered card to the front */
}

.wrapper {
    display: flex;
	width: 90%;
	justify-content: space-around;
}


  
.card:hover {
  transform: translateY(20px);
}

.card:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 15px;
  background: rgba(0, 0, 0, 0.6);
  z-index: 2;
  transition: 0.5s;
  opacity: 0;
}

.card:hover:before {
  opacity: 1;
}

.card img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 15px;
}

.card .info {
  position: relative;
  z-index: 3;
  color: white;
  opacity: 0;
  transform: translateY(30px);
  transition: 0.5s;
}

.card:hover .info {
  opacity: 1;
  transform: translateY(0px);
}

.card .info h1 {
  margin: 0px;
}

.card .info p {
  letter-spacing: 1px;
  font-size: 15px;
  margin-top: 8px;
}

.card .info button {
  padding: 0.6rem;
  outline: none;
  border: none;
  border-radius: 3px;
  background: white;
  color: black;
  font-weight: bold;
  cursor: pointer;
  transition: 0.4s ease;
}

.card .info button:hover {
  background: dodgerblue;
  color: white;
}

.card .info input {
  padding: 0.6rem;
  border-radius: 3px;
  background: white;
  color: black;
  font-weight: bold;
  cursor: pointer;
  transition: 0.4s ease;
  margin-right:0.5rem;
}

.animated-character {
  font-size: 2em; 
  animation: waving 1s ease-in-out infinite alternate; 
}

@keyframes waving {
  0% { transform: rotate(0deg); }
  50% { transform: rotate(15deg); }
  100% { transform: rotate(0deg); }
}
